:root{
    --color: #333;
}
.MyButton{
    padding: 6px 10px;
    border: 2px solid var(--color);
    border-radius: 5px;
    color: var(--color);
    cursor: pointer;
    background-color: rgba(255,255,255,0);
    transition: .2s;
}
.MyButton:hover{
    background-color: rgba(255,255,255,.4);
}